.PageBottom{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 50px;
}
.Page-logo{
    display: flex;
    align-items: center;
    gap: 20px;

}
.Page-logo p{
    color: #383838;
    font-size: 46px;
    font-weight: 700;
    font-family: 'Pacifico', cursive;
}
.Page-link{
    display: flex;
    list-style: none;
    gap: 50px;
    color: #252525;
    font-size: 20px;
}
.Page-link li{
    cursor: pointer;
}
.Page-icon{
    display: flex;
    gap: 20px;
}
.Page-container{
    padding: 10px;
    padding-bottom: 6px;
    background: #fbfbfb;
    border: 1px solid white;

}
.Page-copyright{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
    width: 100%;
    margin-bottom: 30px;
    color: #252525;
    font-size:20px;
}
.Page-copyright hr{
    width: 80%;
    border: none;
    border-radius: 10px;
    height: 3px;
    background: #c7c7c7;
}